.gallery{
  padding: 0;
  margin: 0;
  list-style: none;
  max-width: calc(100% - 64px);
  margin-left: auto;
  margin-right: auto;
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  grid-gap: 24px;
}
.stats{
  display: inline-flex;
    
}

.material-icons{
  width: 20px;
  margin-right: 10px;
  margin-left: 5px;
}